import React from 'react';
import { MDBRow, MDBCol } from 'mdbreact';
import { Route, Switch } from 'react-router-dom';
import Coba from './coba';

const NotFound = () => {
  return (
    <div style={{ marginTop: '7rem' }}>
      <MDBRow>
        <MDBCol
          md="12"
          className="mb-4 text-center justify-content-center align-items-center"
        >
          <h1 className="display-4 font-weight-bold mb-0 pt-md-5 pt-5">
            404 Not Found
          </h1>
          <h5 className="pt-md-5 pt-sm-2 pt-5 pb-md-5 pb-sm-3 pb-5">
            Mau cari apa??
          </h5>
        </MDBCol>
      </MDBRow>
    </div>
  );
};

const Routes = () => {

  return (
    <Switch>
      <Route exact path="/" component={Coba} />
      <Route render={NotFound} />
    </Switch>
  );
};

export default Routes;
