import React from 'react';
import {
  MDBAnimation,
  MDBView,
  MDBRow,
  MDBCol,
  MDBCard,
  MDBCardBody,
  MDBBtn,
  MDBInput
} from 'mdbreact';
import axios from 'axios';

const Coba = () => {

  const [state, setState] = React.useState({data:[]});

  const handleChange = (e) =>{
    axios.get(`https://api.github.com/users/${e.target.value}/repos`)
      .then(res=>{
        setState({...state,data:res.data})
      })
  }

  return (
    <React.Fragment>
      <MDBAnimation type="zoomIn">
        <MDBView>
          <div style={{ marginTop: '7rem' }}>
            <section className="my-5">
              <MDBRow>
                <MDBCol md="6" className="offset-md-3">
                  <MDBCard>
                    <MDBCardBody>
                      <div className="form-header aqua-gradient accent-1">
                        <h3 className="mt-2"> Search Repositories</h3>
                      </div>
                        <MDBInput
                          label="Input Github Username"
                          type="text"
                          id="form-name"
                          onChange={handleChange}
                        />
                        <ul>
                        {state.data.map(repo=>
                          <li key={repo.id}>{repo.name}</li>
                        )}
                        </ul>
                    </MDBCardBody>
                  </MDBCard>
                </MDBCol>
              </MDBRow>
            </section>
          </div>
        </MDBView>
      </MDBAnimation>
    </React.Fragment>
  );
};

export default Coba;
