import React, { useState } from 'react';
import {
  MDBNavbar,
  MDBNavbarBrand,
  MDBNavbarNav,
  MDBNavItem,
  MDBNavLink,
  MDBNavbarToggler,
  MDBCollapse,
  MDBContainer,
} from 'mdbreact';

const Nav = () => {
  const [state, setState] = useState({
    collapseID: '',
    activeitem: '1',
  });

  const toggleCollapse = collapseID => {
    setState(prevState => ({
      ...state,
      collapseID: prevState.collapseID !== collapseID ? collapseID : '',
    }));
  };

  const overlay = (
    <div
      id="sidenav-overlay"
      style={{ backgroundColor: 'transparent' }}
      onClick={() => {
        toggleCollapse('navbarCollapse');
      }}
    />
  );

  return (
    <div>
      <MDBNavbar color="aqua-gradient" dark expand="md" fixed="top" scrolling>
        <MDBContainer>
          <MDBNavbarBrand>
            <strong className="white-text">GIthub</strong>
          </MDBNavbarBrand>
          <MDBNavbarToggler
            onClick={() => {
              toggleCollapse('navbarCollapse');
            }}
          />
        </MDBContainer>
      </MDBNavbar>
      {state.collapseID && overlay}
    </div>
  );
};

export default Nav;